<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__article-comments">
        <div class="ecom-shopify__article-comments--wrapper">
            <div
                class="ecom-shopify__article--comments--container"
                :class="!exporting ? 'ecom-placeholder-on-builder-mode' : ''"
                :data-ecom-placeholder="exporting ? '' : 'This article disabled comments'"
                v-html="liquid('comments')"
            />
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';
export default {
    name: 'ArticleTitle',
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        css() {
            return `
            .ecom-shopify__comment-content footer {
                opacity: 1;
                flex-wrap: wrap;
            }
            .ecom-shopify__article-comments{
                display:flex;
                flex-direction:column;
            }
            .ecom-shopify__article-pagination{
                margin: auto;
                text-align:center;
            }
            .ecom-shopify__article-pagination li {
                display: inline-flex;
            }

            .ecom-shopify__article-pagination .ecom-shopify__article-pagination--visuallyhidden {
                display: none;
            }
            .ecom-paginate-action span{
                display:flex;
            }
            .ecom-paginate-action{
                display:inline-flex !important;
                grid-column-gap:12px;
                align-items:center;
            }
            .ecom-shopify__article-pagination-navigation{
                display: flex;
                justify-content: center;
                align-items:center;
                list-style:none;
            }
            .ecom-paginate-action,
            .ecom-pagination-item a{
                text-decoration: none;
                color:inherit;
            }
            .ecom-paginate-action.ecom-disabled {
                opacity: 0.5;
            }
            .ecom-collection__pagination-navigation{
                display:none;
                align-items: center;
                justify-content:center;
            }
            .ecom-collection__pagination-navigation li{
                display:flex;
            }
            .ecom-paginate-action span{
                display:flex;
            }
            .ecom-shopify__article-comments-icon svg {
                width: 16px;
                height: 16px;
            }
        `;
        },

        liquids() {
            return {
                comments: {
                    code: `
                        {%- if article != empty -%}
                            {% if article.comments_enabled? %}
                                <div class="ecom-shopify__article-comment-wrapper">
                                    <div id="comments" class="ecom-shopify__article-comments">
                                        {%- if article.comments_count == 0 -%}
                                            <p>${this.lang('No comments', 'no_comment')}</p>
                                        {%- endif -%}
                                        {%- if article.comments_count > 0 -%}
                                            {%- assign anchorId = '#comments-' | append: article.id -%}
                                            {% paginate article.comments by ${this.data.settings?.limit_comments ? this.data.settings?.limit_comments : 5 } %}
                                                <div class="ecom-shopify__article-comments">
                                                {%- if comment.status == 'pending' and comment.content -%}
                                                    <article id="{{ comment.id }}" class="ecom-shopify__article-comments-comment ecom-flex ecom-column">
                                                        ${this.data.settings?.show_gavatar
                                                            ? `
                                                                {%- if comment.email -%}
                                                                    <div class="ecom-shopify__comment-author-image ecom-col-auto">
                                                                        <div class="ecom-image-default">
                                                                            <img alt="{{ comment.author }}" src="https://www.gravatar.com/avatar/{{ comment.email | downcase | md5 }}" />
                                                                        </div>
                                                                    </div>
                                                                {%- endif -%}
                                                            `
                                                            : ''
                                                        }
                                                        <div class="ecom-shopify__comment-content ecom-col">
                                                            {{ comment.content }}
                                                            <footer class="right">
                                                                <div class="ecom-flex ecom-al_center ecom-shopify__article-comments-meta">

                                                                    ${this.data.settings?.icon_name
                                                                        ? `
                                                                            <span class="ecom-shopify__article-comments-icon ecom-flex ecom-al_center">${this.data.settings?.icon_name}</span>
                                                                        `
                                                                        : ''
                                                                    }
                                                                    <span class="ecom-shopify__article-circle-divider ecom-shopify__article-caption-with-letter-spacing">{{ comment.author }}</span>
                                                                </div>
                                                            </footer>
                                                        </div>
                                                    </article>
                                                {%- endif -%}

                                                {%- for comment in article.comments -%}
                                                    <article id="{{ comment.id }}" class="ecom-shopify__article-comments-comment ecom-flex">
                                                        ${this.data.settings?.show_gavatar
                                                            ? `
                                                                {%- if comment.email -%}
                                                                    <div class="ecom-shopify__comment-author-image ecom-col-auto">
                                                                        <div class="ecom-image-default">
                                                                            <img alt="{{ comment.author }}" src="https://www.gravatar.com/avatar/{{ comment.email | downcase | md5 }}" />
                                                                        </div>
                                                                    </div>
                                                                {%- endif -%}
                                                            `
                                                            : ''
                                                        }
                                                        <div class="ecom-shopify__comment-content ecom-col">
                                                            {{ comment.content }}
                                                            <footer class="ecom-flex">
                                                                <div class="ecom-flex ecom-al_center">
                                                                    ${this.data.settings?.icon_name
                                                                        ? `
                                                                            <span class="ecom-shopify__article-comments-icon ecom-flex ecom-al_center">${this.data.settings?.icon_name}</span>
                                                                        `
                                                                        : ''
                                                                    }
                                                                    <span class="ecom-shopify__article-circle-divider ecom-shopify__article-caption-with-letter-spacing">{{ comment.author }}</span>
                                                                </div>
                                                                <div class="ecom-flex ecom-al_center ecom-shopify__article-comments-meta">

                                                                    ${this.data.settings?.icon_time
                                                                            ? `
                                                                                <span class="ecom-shopify__article-comments-icon ecom-flex ecom-al_center">${this.data.settings?.icon_time}</span>
                                                                            `
                                                                            : ''
                                                                        }
                                                                    <span class="caption-with-letter-spacing">{{ comment.created_at | time_tag: format:  "${this.data.settings?.date_format ??'basic'}" }}</span>
                                                                </div>
                                                            </footer>
                                                        </div>
                                                    </article>
                                                {%- endfor -%}
                                                {%- if paginate.pages > 1 -%}
                                    <nav role="navigation">
                                    <ol class="ecom-pagination-navigation ecom-collection__pagination-navigation">
                                        {%- if paginate.previous -%}
                                            <li class="ecom-pagination-item ecom-prev" style="${
                                                this.data.settings?.pagination_style === 'block'? 'margin-right:auto': ''
                                            }">
                                                <a class="ecom-paginate-action ecom-button-default" href="{{ paginate.previous.url }}">
                                                ${this.data.settings?.number_type === 'icon' || this.data.settings?.number_type === 'text_icon' ?
                                                `<span class="ecom-paginate-action--icon">${this.data.settings?.icon_prev_page || ''}</span>`
                                                : ''
                                                }
                                                ${this.data.settings?.number_type !== 'icon' ? this.lang(this.data.settings?.text_prev_page,'prev_page') : ''}
                                                </a>
                                            </li>
                                            {%- else -%}
                                            <li class="ecom-pagination-item ecom-prev ecom-paginate-action ecom-disabled"
                                            style="${
                                                this.data.settings?.pagination_style == 'block' ? 'margin-right:auto': ''}"
                                            >
                                                ${ this.data.settings?.number_type === 'icon' || this.data.settings?.number_type === 'text_icon'
                                                ? `<span class="ecom-paginate-action--icon">${this.data.settings?.icon_prev_page || ''}</span>`
                                                : ''
                                                }
                                                ${this.data.settings?.number_type !== 'icon' ? this.lang(this.data.settings?.text_prev_page,'prev_page'): ''}
                                            </li>
                                            {%- endif -%}
                                            {%- for part in paginate.parts -%}
                                                {%- if part.is_link -%}
                                                    <li class="ecom-pagination-item">
                                                        <a href="{{ part.url }}" title="{{ part.title }}">
                                                            {{ part.title }}
                                                        </a>
                                                    </li>
                                                {%- else -%}
                                                    {%- if part.title == paginate.current_page -%}
                                                    <li class="ecom-pagination-item ecom-button-active" aria-current="page">
                                                        {{ part.title }}
                                                    </li>
                                                    {%- else -%}
                                                    <li class="ecom-pagination-item">
                                                        {{ part.title }}
                                                    </li>
                                                    {%- endif -%}
                                                {%- endif -%}
                                            {%- endfor -%}

                                            {%- if paginate.next -%}
                                            <li class="ecom-pagination-item ecom-next" style="${this.data.settings?.pagination_style === 'block'? 'margin-left:auto': ''}">
                                                <a class="ecom-paginate-action" href="{{ paginate.next.url }}">
                                                    ${this.data.settings?.number_type !== 'icon' ? this.lang( this.data.settings?.text_next_page,  'next_page'): '' }

                                                    ${ this.data.settings?.number_type == 'icon' || this.data.settings?.number_type === 'text_icon'
                                                    ? `<span class="ecom-paginate-action--icon">${this.data.settings?.icon_next_page || ''}</span>`
                                                    : ''
                                                    }
                                                </a>
                                            </li>
                                            {%- else -%}
                                            <li class="ecom-pagination-item ecom-next ecom-paginate-action ecom-collection__pagination--disabled"
                                            style="${this.data.settings?.pagination_style === 'block' ? 'margin-left:auto' : ''}">
                                                ${this.data.settings?.number_type !== 'icon'? this.lang(this.data.settings?.text_next_page,'next_page'): ''}
                                                ${this.data.settings?.number_type == 'icon' || this.data.settings?.number_type == 'text_icon'
                                                ? `<span class="ecom-paginate-action--icon">${this.data.settings?.icon_next_page || ''}</span>`
                                                : ''
                                                }
                                            </li>
                                        {%- endif -%}
                                    </ol>
                                    </nav>
                                {%- endif -%}
                                                </div>
                                            {% endpaginate %}
                                        {%- endif -%}
                                    </div>
                                </div>
                            {% endif %}

                        {%- else -%}
                            <div>Please select the article in settings</div>
                        {% endif %}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                }
            };
        },

        requestShopifyType () {
            return {
                shopify_type: 'article'
            };
        },

        settings() {
            return [
                {
                    group_title:'General',
                    params: [
                        {
                            type: 'popup',
                            name: 'date_format',
                            label: 'Date format',
                            options: {
                                type: 'dropdown',
                                values: {
                                    default: 'Monday, December 31, 2018 at 1:00 pm -0500',
                                    abbreviated_date: 'Aug 08, 1994',
                                    basic: '12/31/2021',
                                    date: 'December 31, 2018',
                                    date_at_time: 'December 31, 2018 at 1:00 pm',
                                    on_date: 'on Dec 31, 2018'
                                }
                            }
                        },
                        {
                            type:'number',
                            label:'Comments per page',
                            name:'limit_comments',
                            options:{
                                min:1,
                                max:20
                            }
                        },
                        {
                            type:'number',
                            label:'Comment gap',
                            name:'gap_comments',
                            options:{
                                reset:false,
                                units:{
                                    "px":{
                                        min:0,
                                        max:50
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom-shopify__article-comments',
                                properties:{
                                    gap:''
                                }
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Comment position',
                            name: 'comment_position',
                            options: {
                                type: 'align-y',
                                values: [1, -1],
                            },
                            css:{
                                selector:' .ecom-shopify__article-comments-comment footer',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: 'line'
                        },
                        {
                            type: 'toggle',
                            label: 'Show G-Avatar',
                            name: 'show_gavatar',
                            value: true,
                            options: {
                                values:{
                                    on:{
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Icon name',
                            name: 'icon_name',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false,
                                visible: {
                                    keep_data: false,
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Icon time',
                            name: 'icon_time',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false,
                                visible: {
                                    keep_data: false,
                                }
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Icon position',
                            name: 'icon_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon_name'] || data['icon_time'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom-shopify__article-comments-icon',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Icon spacing',
                            name: 'icon_spacing',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 200
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon_name'] || data['icon_time'];
                                    }
                                }
                            },
                            css:{
                                selector:' footer > div',
                                properties:{
                                    gap:''
                                }
                            }
                        },
                    ]
                },
                {
                    group_alias:'pagination:settings'
                }
            ];
        },

        default() {
            return {
                "settings": {
                    "text_prev_page": "Previous",
                    "text_next_page": "Next",
                    "number_type": "text",
                    "limit_comments": 5,
                    "enable_pagination": true,
                    "pagination_style": "inline",
                    "grid-column-gap": "10px",
                    "gap_comments": "16px"
                },
                "style": {
                    "general": {
                        "text-align": "left"
                    },
                    "comments": {
                        "spacing": {
                            "margin": {
                                "bottom": "4px"
                            }
                        },
                        "textTypography": {
                            "font-style": "italic"
                        }
                    },
                    "author": {
                        "spacing": {
                            "margin": {
                                "right": "8px"
                            }
                        },
                        "textTypography": {
                            "font-size": "18px",
                            "text-transform": "capitalize"
                        },
                        "textColor": "#050000"
                    },
                    "panigation_button": {
                        "buttonAlignment": "center",
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "rgba(17, 24, 39, 0.1)"
                            }
                        },
                        "buttonColornormalmode": "#111827",
                        "buttonColorhovermode": "#111827",
                        "buttonBackgroundhovermode": {
                            "classic": {
                                "background-color": "rgba(17, 24, 39, 0.2)"
                            }
                        },
                        "buttonColoractivemode": "#ffffff",
                        "buttonBackgroundactivemode": {
                            "classic": {
                                "background-color": "#111827"
                            }
                        },
                        "padding": {
                            "left": "12.5px",
                            "top": "4px",
                            "bottom": "4px",
                            "right": "12.5px"
                        },
                        "spacing": {
                            "margin": {
                                "top": "30px"
                            }
                        },
                        "tab": "normal",
                        "iconFontSize": "30px"
                    }
                }
            }
        }

    },
    methods:{
        style() {
            return [
                {
                    group_alias: 'box',
                    options: {
                        group_name: 'general',
                        group_title: 'General'
                    },
                    modify: {
                        params: {
                            alias: 'text-align',
                            options: {
                                label: 'Alignment'
                            }
                        }
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_name: 'comments',
                        group_title: 'Comments',
                        selector: ' .ecom-shopify__article-comments-comment p'
                    },
                    modify: {
                        remove: {
                            index: 0, length: 1
                        }
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_name: 'author',
                        group_title: 'Author',
                        selector: ' .ecom-shopify__article-comments-comment span.ecom-shopify__article-caption-with-letter-spacing'
                    },
                    modify: {
                        remove: {
                            index: 0, length: 1
                        }
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_name: 'datetime',
                        group_title: 'Date',
                        selector: ' .ecom-shopify__article-comments-comment time'
                    },
                    modify: {
                        remove: {
                            index: 0, length: 1
                        }
                    }
                },
                {
                    group_alias: 'pagination',
                    options: {
                        group_title: 'Pagination',
                        group_name: 'panigation_button',
                        selector: ' .ecom-pagination-navigation'
                    },
                    modify: {
                        params:
                        this.data.settings?.pagination_style !== 'block'
                            ? {
                                type: 'choose',
                                name: 'buttonAlignment',
                                label: 'Alignment',
                                options: {
                                    responsive: true,
                                    type: 'text-align',
                                    values: ['flex-start', 'center', 'flex-end']
                                },
                                css: {
                                    properties: {
                                        display: 'flex',
                                        'justify-content': ''
                                    }
                                }
                            }
                            : null
                    }
                },
                {
                    group_alias: 'image',
                    options: {
                        group_title: 'G Avatar',
                        group_name: 'gavatar',
                        selector: ' .ecom-shopify__comment-author-image'
                    }
                },
                {
                    group_alias:'icon',
                    options:{
                        group_name: 'icon_time',
                        group_title: 'Icon',
                        selector: ' .ecom-shopify__article-comments-icon',
                    },
                    modify:{
                        params:[
                            {
                                position:15,
                                fields:[
                                    {
                                        type: 'line'
                                    },
                                    {
                                        alias:'spacing'
                                    }
                                ]
                            }
                        ]
                    }
                }
            ];
        }
    }
};
</script>